import React, { Component } from "react";
import { Form, Button } from "semantic-ui-react";

export default class UserProfileEditForm extends Component {
  render() {
    return (
      <Form>
        <Form.Field>
          <label>First Name</label>
          <input placeholder={this.props.userData.first_name} />
        </Form.Field>
        <Form.Field>
          <label>Last Name</label>
          <input placeholder={this.props.userData.last_name} />
        </Form.Field>
        <Form.Field>
          <label>Email</label>
          <input placeholder={this.props.userData.email} />
        </Form.Field>
        <Form.Field>
          <label>New Password</label>
          <input />
        </Form.Field>
        <Form.Field>
          <label>New Password Confirmation</label>
          <input />
        </Form.Field>
        <Button type="submit">Edit Profile</Button>
      </Form>
    );
  }
}
